.app_container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.app_content {
  display: flex;
  height: calc(100vh - 60px);
}

.a1 {
  flex: 1;
  padding: 0 75px;
}

.a2 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.a2-img {
  width: 100%;
  height: 100%;
}

.app_child {
  display: flex;
  justify-content: space-between;
  padding-bottom: 20px;
}


@media screen and (max-width: 1400px) {
  .app_child {
    display: flex;
    flex-direction: column;
  }

  .skills_container {
    padding: 8px 0;
  }

  .about_container {
    padding-bottom: 16px;
    padding-top: 0;
  } 
}

@media screen and (max-width: 800px) {
  .header_content {
    padding: 0 16px;
  }

  .menu_btn {
    padding: 0 4px;
  }

  .a1 {
    padding: 0 16px; 
  }

  .about_container {
    padding-bottom: 8px;
  }

  .about_title {
    font-size: 24px;
  }

  .about_subtitle {
    font-size: 15px;
  }

  .about_description {
    font-size: 14px;
  }

  .experience_container {
    padding: 8px 0;
  }

  .skills_container {
    padding: 8px 0;
  }

  .skills_list {
    width: 100%;
  }

  .social_media_container {
    padding: 8px 0 24px 0;
  }

  .a2 {
    display: none;
  }
}

@media screen and (max-height: 200px) {
  .about_container {
    padding-bottom: 16px;
    padding-top: 0;
  }

  .app_child {
    padding-bottom: 0;
  }

  .a1 {
    padding: 0 16px; 
  }
}
